.anime-like {
  animation: marginlike 5s infinite;
  transition: transform 1s ease-out 0.5s; }

@mixin animation() {
  0% {
    transform: rotate(0deg) translate(-50%, -50%); }
  25% {
    transform: rotate(90deg) translate(-50%, -50%); }
  50% {
    transform: rotate(180deg) translate(-50%, -50%); }
  75% {
    transform: rotate(270deg) translate(-50%, -50%); }
  100% {
    transform: rotate(360deg) translate(-50%, -50%); } }

@-webkit-keyframes play_button {
  @include animation(); }
@-moz-keyframes play_button {
  @include animation(); }
@-o-keyframes play_button {
  @include animation(); }
@keyframes play_button {
  @include animation(); }

@mixin marginanimation() {
  0% {
    opacity: 1; }
  25% {
    opacity: 0; }
  50% {
    opacity: 1; }
  75% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes margin {
  @include marginanimation(); }
@-moz-keyframes margin {
  @include marginanimation(); }
@-o-keyframes margin {
  @include marginanimation(); }
@keyframes margin {
  @include marginanimation(); }


@mixin marginanimation2() {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes margin2 {
  @include marginanimation2(); }
@-moz-keyframes margin2 {
  @include marginanimation2(); }
@-o-keyframes margin2 {
  @include marginanimation2(); }
@keyframes margin2 {
  @include marginanimation2(); }

@mixin likeanimation() {
  0% {
    transform: translate(0, -30px); }
  25% {
    transform: translate(0, 20px); }
  50% {
    transform: translate(0, -30px); }
  75% {
    transform: translate(0, 20px); }
  100% {
    transform: translate(0, -30px); } }

@-webkit-keyframes marginlike {
  @include likeanimation(); }
@-moz-keyframes marginlike {
  @include likeanimation(); }
@-o-keyframes marginlike {
  @include likeanimation(); }
@keyframes marginlike {
  @include likeanimation(); }

